import {h} from 'preact';

const Input = ({
  className,
  type='text',
  value,
  onChange,
  min=0,
  max,
  step=1
})=>{
  return (
    <input
     className= { className }
     type= { type }
     value = { value }
     onChange = { onChange }
     min = { min }
     max = { max }
     step = { step }
    />
  )
}

export default Input;